import React from 'react'

import ViewBodyComponent from './view-body/view-body.component'
import ViewFooterComponent from './view-footer/view-footer.component'
import ViewHeaderComponent from './view-header/view-header.component'

function DataViewComponent(props) {
  return (
    <>
      <ViewHeaderComponent closeDataView={props.closeDataView} />
      <ViewBodyComponent data={props.data} />
      <ViewFooterComponent />
    </>
  )
}

export default DataViewComponent
