import React from 'react'

import styles from './view-body.module.css'

function ViewBodyComponent(props) {
  const successClassName =
    props.data.status === 'Success' ? styles.successStatus : null
  const runningClassName =
    props.data.status === 'Running' ? styles.runningStatus : null
  const failedClassName =
    props.data.status === 'Failed' ? styles.failedStatus : null
  const stoppedClassName =
    props.data.status === 'Stopped' ? styles.stoppedStatus : null
  const queuedClassName =
    props.data.status === 'Queued' ? styles.queuedStatus : null

  return (
    <div className={styles.body}>
      <p className={styles.title}>{props.data.title}</p>

      <div
        className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
      >
        {props.data.status}
      </div>

      <div className={styles.details}>
        <div className="row">
          <div className="col-6">
            <p className={styles.detailLabel}>
              Client <br />{' '}
              <span className={styles.detailText}>{props.data.client}</span>
            </p>
            <p className={styles.detailLabel}>
              Started <br />{' '}
              <span className={styles.detailText}>{props.data.started}</span>
            </p>
            <p className={styles.detailLabel}>
              Duration <br />{' '}
              <span className={styles.detailText}>{props.data.duration}</span>
            </p>
            <p className={styles.detailLabel}>
              Worker ID <br />{' '}
              <span className={styles.detailText}>{props.data.workerId}</span>
            </p>
          </div>
          <div className="col-6">
            <p className={styles.detailLabel}>
              Worker <br />{' '}
              <span className={styles.detailText}>{props.data.worker}</span>
            </p>
            <p className={styles.detailLabel}>
              Ended <br />{' '}
              <span className={styles.detailText}>{props.data.ended}</span>
            </p>
            <p className={styles.detailLabel}>
              Modified <br />{' '}
              <span className={styles.detailText}>7 hours ago</span>
            </p>
            <p className={styles.detailLabel}>
              Environment <br />{' '}
              <span className={styles.detailText}>Production</span>
            </p>
          </div>
        </div>
      </div>

      <hr style={{ backgroundColor: '#b1b1b1' }} />

      <div className={styles.details}>
        <div className="row">
          <div className="col-6">
            <p className={styles.detailLabel}>
              Trigger Name <br />{' '}
              <span className={styles.detailText}>
                ITV - Silvermouse Data Entry
              </span>
            </p>
            <p className={styles.detailLabel}>
              Trigger Type <br />{' '}
              <span className={styles.detailText}>Email</span>
            </p>
            <p className={styles.detailLabel}>
              Trigger Email Sender <br />{' '}
              <span className={styles.detailText}>support@virtualai.co.uk</span>
            </p>
            <p className={styles.detailLabel}>
              Process ID <br /> <span className={styles.detailText}>16</span>
            </p>
            <p className={styles.detailLabel}>
              Queued <br />{' '}
              <span className={styles.detailText}>18/08/2021 8:44:00 AM</span>
            </p>
            <p className={styles.detailLabel}>
              Group <br /> <span className={styles.detailText}>aCAA001</span>
            </p>
          </div>
          <div className="col-6">
            <p className={styles.detailLabel}>
              Trigger ID <br /> <span className={styles.detailText}>18</span>
            </p>
            <p className={styles.detailLabel}>
              Trigger Subject <br />{' '}
              <span className={styles.detailText}>
                Music Cue Sheets process - PRS Processing File
              </span>
            </p>
            <p className={styles.detailLabel}>
              Output <br />{' '}
              <span className={styles.detailText}>{props.data.status}</span>
            </p>
            <p className={styles.detailLabel}>
              Proccess <br /> <span className={styles.detailText}>16</span>
            </p>
            <p className={styles.detailLabel}>
              Group ID <br /> <span className={styles.detailText}>871</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ViewBodyComponent
