import React, { useState } from 'react'
import FilterButtonComponent from '../filter-button/filter-button.component'
import styles from './starts-with-filter.module.css'

function StartsWithFilterComponent(props) {
  const options = [
    {
      optionId: 1,
      optionText: 'Starts With',
    },
    {
      optionId: 2,
      optionText: 'Ends With',
    },
    {
      optionId: 3,
      optionText: 'Contains With',
    },
    {
      optionId: 4,
      optionText: 'Equal',
    },
    {
      optionId: 5,
      optionText: 'Not Equal',
    },
  ]
  const [selectedOption, setSelectedOption] = useState(options[0])
  const [showOptions, setShowOptions] = useState(false)

  const toggleOptions = () => {
    setShowOptions(!showOptions)
  }

  const selectOption = (option) => {
    setSelectedOption(option)
    props.onChangedFilterModel(props.filterKey, option.optionId)
    setShowOptions(false)
  }

  const bindInput = (e) => {
    props.onChangedFilterModel(props.filterValueKey, e.target.value)
    if (selectedOption.optionId === options[0].optionId) {
      selectOption(options[0])
    }
  }

  return (
    <div className="p-3">
      <div></div>
      <div className="position-relative">
        <button className={styles.startsWithButton} onClick={toggleOptions}>
          {selectedOption.optionText}
        </button>
        <div
          className={styles.options}
          style={{
            display: showOptions ? null : 'none',
            marginBottom: `-${25 * options.length}px`,
            height: `${25.25 * options.length}px`,
          }}
        >
          {options.map((value, index) => {
            return (
              <div
                className={styles.optionItem}
                key={index}
                onClick={() => selectOption(options[index])}
              >
                <span>{value.optionText}</span>
              </div>
            )
          })}
        </div>
      </div>
      <br />
      <input
        className={styles.startsWithInput}
        type="text"
        placeholder="Enter the value"
        onChange={bindInput}
      />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <FilterButtonComponent
          onClickedButtonFilter={props.onClickedButtonFilter}
          toggleDropdown={props.toggleDropdown}
        />
      </div>
    </div>
  )
}

export default StartsWithFilterComponent
