import React, { useState } from 'react'

import styles from './client-filter.module.css'
import checkIcon from '../../../../../../assets/icons/Check.png'
import FilterButtonComponent from '../filter-button/filter-button.component'

function ClientFilterComponent(props) {
  const [clientArr, setClientArr] = useState([
    {
      clientName: 'Select All',
      clientState: false,
    },
    {
      clientName: 'Astro',
      clientState: false,
    },
    {
      clientName: 'ITV',
      clientState: false,
    },
    {
      clientName: 'MGM',
      clientState: false,
    },
    {
      clientName: 'Turner',
      clientState: false,
    },
  ])
  const [clientFilterArr] = useState([])

  const emitClientFilter = (newArr) => {
    props.onChangedFilterModel('clients', newArr.join('-'))
  }

  const pushClientFilterItem = (index) => {
    clientFilterArr.push(clientArr[index].clientName)
    var newArr = clientFilterArr
    emitClientFilter(newArr)
  }

  const removeClientFilterItem = (value) => {
    const i = clientFilterArr.indexOf(value)
    if (i > -1) {
      clientFilterArr.splice(i, 1)
    }
    emitClientFilter(clientFilterArr)
  }

  const selectAllBox = (index) => {
    let newClientArr = [...clientArr]

    if (index === 0) {
      if (newClientArr[0].clientState) {
        for (var i = 1; i < clientArr.length; i++) {
          newClientArr[i].clientState = true
          setClientArr(newClientArr)
        }
      } else {
        for (var j = 1; j < clientArr.length; j++) {
          newClientArr[j].clientState = false
          setClientArr(newClientArr)
        }
      }
    }
  }

  const selectBox = (index) => {
    let newClientArr = [...clientArr]
    newClientArr[index].clientState = !newClientArr[index].clientState
    setClientArr(newClientArr)

    selectAllBox(index)

    if (clientArr[index].clientState === true) {
      pushClientFilterItem(index)
    } else {
      removeClientFilterItem(clientArr[index].clientName)
    }
  }

  return (
    <>
      <input
        className={styles.dropdownSearch}
        type="search"
        placeholder="Search"
      />
      <div className={styles.clientList}>
        <div style={{ height: '100px', overflowY: 'auto' }}>
          {clientArr.map((value, index) => {
            return (
              <div className={styles.item} key={index}>
                <div
                  className={`${styles.checkbox} ${
                    value.clientState ? styles.selectedClient : null
                  }`}
                  style={{ marginRight: '10px' }}
                  onClick={() => selectBox(index)}
                >
                  <img
                    className={styles.checkIcon}
                    src={checkIcon}
                    alt="CheckIconAll"
                  />
                </div>
                {value.clientName}
              </div>
            )
          })}
        </div>
      </div>
      <div className="px-4">
        <hr style={{ backgroundColor: '#6d6d73' }} />
      </div>
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <FilterButtonComponent
          onClickedButtonFilter={props.onClickedButtonFilter}
          toggleDropdown={props.toggleDropdown}
        />
      </div>
    </>
  )
}

export default ClientFilterComponent
