import React, { useState } from 'react'
import FilterButtonComponent from '../filter-button/filter-button.component'
import styles from './duration-filter.module.css'

function DurationFilterComponent(props) {
  const options = [
    {
      optionId: 1,
      optionText: 'Equal',
    },
    {
      optionId: 2,
      optionText: 'Greater Than',
    },
    {
      optionId: 3,
      optionText: 'Greater Than or Equal',
    },
    {
      optionId: 4,
      optionText: 'Less than',
    },
    {
      optionId: 5,
      optionText: 'Less than or Equal',
    },
    {
      optionId: 6,
      optionText: 'No Equal',
    },
  ]
  const [timeValue, setTimeValue] = useState({
    hour: '00',
    minute: '00',
    second: '00',
  })
  const [buttonText, setButtonText] = useState('Equal')
  const [showOptions, setShowOptions] = useState(false)

  const toggleOptions = () => {
    setShowOptions(!showOptions)
  }

  const selectOption = (option) => {
    setButtonText(option.optionText)
    props.onChangedFilterModel('durationFilter', option.optionId)
    setShowOptions(false)
  }

  const bindInput = (e) => {
    setTimeValue((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }))
    const time =
      timeValue.hour + ':' + timeValue.minute + ':' + timeValue.second
    props.onChangedFilterModel('durationFilterValue', time)
  }

  return (
    <div className="p-3">
      <div></div>
      <div className="position-relative">
        <button className={styles.timeRangeButton} onClick={toggleOptions}>
          {buttonText}
        </button>
        <div
          className={styles.options}
          style={{
            display: showOptions ? null : 'none',
            marginBottom: `-${25 * options.length}px`,
            height: `${25.25 * options.length}px`,
          }}
        >
          {options.map((value, index) => {
            return (
              <div
                className={styles.optionItem}
                onClick={() => selectOption(options[index])}
                key={index}
              >
                <span>{value.optionText}</span>
              </div>
            )
          })}
        </div>
      </div>
      <br />
      <input
        className={styles.hoursInput}
        type="number"
        placeholder="HH"
        name="hour"
        onChange={bindInput}
      />
      <input
        className={styles.minutesInput}
        type="number"
        placeholder="MM"
        name="minute"
        onChange={bindInput}
      />
      <input
        className={styles.secondsInput}
        type="number"
        placeholder="SS"
        name="second"
        onChange={bindInput}
      />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <FilterButtonComponent
          onClickedButtonFilter={props.onClickedButtonFilter}
          toggleDropdown={props.toggleDropdown}
        />
      </div>
    </div>
  )
}

export default DurationFilterComponent
