import React from 'react'

import styles from './view-header.module.css'

function ViewHeaderComponent(props) {
  return (
    <div className={styles.header}>
      <div
        className={styles.closeButton}
        onClick={() => props.closeDataView()}
      ></div>
      <div className={styles.tabs}>
        <div className={`${styles.tabItem} ${styles.activeTabItem}`}>
          <span>Job Details</span>
        </div>
        <div className={styles.tabItem}>
          <span>Logs</span>
        </div>
        <div className={styles.tabItem}>
          <span>Screens</span>
        </div>
      </div>
    </div>
  )
}

export default ViewHeaderComponent
