import React from 'react'
import styles from './filter-button.module.css'

function FilterButtonComponent(props) {
  const onClickedButtonFilter = () => {
    props.onClickedButtonFilter()
    props.toggleDropdown(null)
  }

  return (
    <button className={styles.filterButton} onClick={onClickedButtonFilter}>
      Filter
    </button>
  )
}

export default FilterButtonComponent
