import React, { useState } from 'react'

import styles from './status-filter.module.css'
import checkIcon from '../../../../../../assets/icons/Check.png'
import FilterButtonComponent from '../filter-button/filter-button.component'

function StatusFilterComponent(props) {
  const [statusArr, setStatusArr] = useState([
    {
      statusName: 'Select All',
      statusState: false,
    },
    {
      statusName: 'Running',
      statusState: false,
    },
    {
      statusName: 'Queued',
      statusState: false,
    },
    {
      statusName: 'Success',
      statusState: false,
    },
    {
      statusName: 'Failed',
      statusState: false,
    },
    {
      statusName: 'Stopped',
      statusState: false,
    },
  ])
  const [statusFilterArr] = useState([])

  const emitStatusFilter = (newArr) => {
    props.onChangedFilterModel('status', newArr.join('-'))
  }

  const pushStatusFilterItem = (index) => {
    statusFilterArr.push(statusArr[index].statusName)
    var newArr = statusFilterArr
    emitStatusFilter(newArr)
  }

  const removeStatusFilterItem = (value) => {
    const i = statusFilterArr.indexOf(value)
    if (i > -1) {
      statusFilterArr.splice(i, 1)
    }
    emitStatusFilter(statusFilterArr)
  }

  const selectAllBox = (index) => {
    let newStatusArr = [...statusArr]

    if (index === 0) {
      if (newStatusArr[0].statusState) {
        for (var i = 1; i < statusArr.length; i++) {
          newStatusArr[i].statusState = true
          setStatusArr(newStatusArr)
        }
      } else {
        for (var j = 1; j < statusArr.length; j++) {
          newStatusArr[j].statusState = false
          setStatusArr(newStatusArr)
        }
      }
    }
  }

  const selectBox = (index) => {
    let newStatusArr = [...statusArr]
    newStatusArr[index].statusState = !newStatusArr[index].statusState
    setStatusArr(newStatusArr)

    selectAllBox(index)

    if (statusArr[index].statusState === true) {
      pushStatusFilterItem(index)
    } else {
      removeStatusFilterItem(statusArr[index].statusName)
    }
  }

  return (
    <>
      <div className="p-3 py-2">
        <div></div>
        {statusArr.map((value, index) => {
          const selectAllStatusClassName =
            value.statusName === 'Select All' ? styles.selectAllStatus : null
          const runningClassName =
            value.statusName === 'Running' ? styles.runningStatus : null
          const queuedClassName =
            value.statusName === 'Queued' ? styles.queuedStatus : null
          const successClassName =
            value.statusName === 'Success' ? styles.successStatus : null
          const failedClassName =
            value.statusName === 'Failed' ? styles.failedStatus : null
          const stoppedClassName =
            value.statusName === 'Stopped' ? styles.stoppedStatus : null
          return (
            <div className={`${styles.item} d-flex`} key={index}>
              <div
                className={`${styles.checkbox} ${
                  value.statusState ? styles.selectedStatus : null
                }`}
                onClick={() => selectBox(index)}
              >
                <img
                  className={styles.checkIcon}
                  src={checkIcon}
                  alt="CheckIcon1"
                />
              </div>
              <div
                className={`${selectAllStatusClassName} ${runningClassName} ${queuedClassName} ${successClassName} ${failedClassName} ${stoppedClassName}`}
              >
                <span>{value.statusName}</span>
              </div>
            </div>
          )
        })}

        <hr style={{ backgroundColor: '#6d6d73' }} />

        <div className="d-flex">
          <p className={styles.clearFilterButton}>Clear</p>
          <FilterButtonComponent
            onClickedButtonFilter={props.onClickedButtonFilter}
            toggleDropdown={props.toggleDropdown}
          />
        </div>
      </div>
    </>
  )
}

export default StatusFilterComponent
