import React, { useState } from 'react'
import { Route, Switch, Redirect, useLocation } from 'react-router'

import styles from './views.module.css'

import MonitoringComponent from '../components/monitoring/monitoring.component'
import SidebarComponent from '../shared/sidebar/sidebar.component'
import NotFoundComponent from '../components/not-found/notFound.component'
import AnalyticsComponent from '../components/analytics/analytics.component'
import WorkersComponent from '../components/workers/workers.component'
import CredentialsComponent from '../components/credentials/credentials.component'
import ProcessComponent from '../components/process/process.component'
import SettingsComponent from '../components/settings/settings.component'
import HelpComponent from '../components/help/help.component'

function ViewsComponent(props) {
  const location = useLocation()
  const [sidebarState, setSidebarState] = useState()

  return (
    <div className={styles.views}>
      <div
        className={`${styles.sidebar} ${
          sidebarState ? styles.vaiShrinkSidebar : null
        }`}
        style={{
          display: location.pathname === '/not-found' ? 'none' : null,
        }}
      >
        <SidebarComponent
          toggleSidebar={(state) => setSidebarState(state)}
          logoutEvent={props.logoutEvent}
        />
      </div>
      <div
        className={styles.content}
        style={{
          display: location.pathname === '/not-found' ? 'none' : null,
        }}
      >
        <Route exact path="/monitoring" component={MonitoringComponent} />
        <Route exact path="/analytics" component={AnalyticsComponent} />
        <Route exact path="/workers" component={WorkersComponent} />
        <Route exact path="/credentials" component={CredentialsComponent} />
        <Route exact path="/process" component={ProcessComponent} />
        <Route exact path="/settings" component={SettingsComponent} />
        <Route exact path="/help" component={HelpComponent} />
      </div>
      <Switch>
        <Route
          exact
          path="/"
          render={() => {
            return <Redirect to="/monitoring" />
          }}
        />
        <Route
          exact
          path="/login"
          render={() => {
            return <Redirect to="/monitoring" />
          }}
        />
        <Route
          exact
          path="/monitoring"
          render={() => {
            return <Redirect to="/monitoring" />
          }}
        />
        <Route
          exact
          path="/analytics"
          render={() => {
            return <Redirect to="/analytics" />
          }}
        />
        <Route
          exact
          path="/workers"
          render={() => {
            return <Redirect to="/workers" />
          }}
        />
        <Route
          exact
          path="/credentials"
          render={() => {
            return <Redirect to="/credentials" />
          }}
        />
        <Route
          exact
          path="/process"
          render={() => {
            return <Redirect to="/process" />
          }}
        />
        <Route
          exact
          path="/settings"
          render={() => {
            return <Redirect to="/settings" />
          }}
        />
        <Route
          exact
          path="/help"
          render={() => {
            return <Redirect to="/help" />
          }}
        />
        <Route path="/not-found" component={NotFoundComponent} />
        <Route render={(props) => <Redirect to="/not-found" />} />
      </Switch>
    </div>
  )
}

export default ViewsComponent
