import React from 'react'

import styles from './view-footer.module.css'
import pauseIcon from '../../../assets/icons/Pause.png'
import stopIcon from '../../../assets/icons/Stop.png'
import horizontalEllipsis2Icon from '../../../assets/icons/Horizontal-Ellipsis-2.png'

function ViewFooterComponent() {
  return (
    <div className={styles.footer}>
      <img className={styles.pauseIcon} src={pauseIcon} alt="PauseIcon" />
      <img className={styles.stopIcon} src={stopIcon} alt="StopIcon" />
      <img
        className={styles.horizontalEllipsis2Icon}
        src={horizontalEllipsis2Icon}
        alt="HorizontalEllipsis2Icon"
      />
    </div>
  )
}

export default ViewFooterComponent
