import axios from 'axios'

class MonitoringTableBodyService {
  getAllWorkers = (queryString) => {
    const url = process.env.REACT_APP_HOST_URL + 'monitoring' + queryString
    alert(url)
    return axios.get(url)
  }
}
const monitoringTableBodyService = new MonitoringTableBodyService()
export default monitoringTableBodyService
