import React, { useState } from 'react'

import styles from './monitoring.table.module.css'
import TableHeaderComponent from './header/monitoring.table.header.component'
import TableBodyComponent from './body/monitoring.table.body.component'
import TableFooterComponent from './footer/monitoring.table.footer.component'
import DataViewComponent from '../../../shared/data-view/data-view.component'

function TableComponent(props) {
  const { useRef } = React
  const tableBodyRef = useRef()
  const [filterModel, setFilterModel] = useState({
    clients: undefined,
    titleFilter: undefined,
    titleFilterValue: undefined,
    workerFilter: undefined,
    workerFilterValue: undefined,
    durationFilter: undefined,
    durationFilterValue: undefined,
    status: undefined,
  })
  const [dataView, setDataView] = useState({})
  const [activeDataView, setActiveDataView] = useState(false)
  var [paginationData, setPaginationData] = useState({})
  const [pageButtons, setPageButtons] = useState([
    {
      buttonText: 1,
    },
    {
      buttonText: 2,
    },
    {
      buttonText: 3,
    },
    {
      buttonText: 4,
    },
    {
      buttonText: 5,
    },
  ])

  const decrementPageButtonNumbers = () => {
    if (paginationData.currentPage > 3) {
      setPageButtons([
        { buttonText: pageButtons[0].buttonText - 1 },
        { buttonText: pageButtons[1].buttonText - 1 },
        { buttonText: pageButtons[2].buttonText - 1 },
        { buttonText: pageButtons[3].buttonText - 1 },
        { buttonText: pageButtons[4].buttonText - 1 },
      ])
    }
  }

  const paginateTo = (currentPage) => {
    if (currentPage <= 2) {
      setPageButtons([
        { buttonText: 1 },
        { buttonText: 2 },
        { buttonText: 3 },
        { buttonText: 4 },
        { buttonText: 5 },
      ])
    } else if (currentPage > 2) {
      setPageButtons([
        { buttonText: currentPage - 2 },
        { buttonText: currentPage - 1 },
        { buttonText: currentPage },
        { buttonText: currentPage + 1 },
        { buttonText: currentPage + 2 },
      ])
    }
  }

  const incrementPageButtonNumbers = () => {
    if (paginationData.currentPage > 2) {
      setPageButtons([
        { buttonText: pageButtons[0].buttonText + 1 },
        { buttonText: pageButtons[1].buttonText + 1 },
        { buttonText: pageButtons[2].buttonText + 1 },
        { buttonText: pageButtons[3].buttonText + 1 },
        { buttonText: pageButtons[4].buttonText + 1 },
      ])
    }
  }

  const displayDataView = (data) => {
    setDataView(data)
    setActiveDataView(true)
  }

  const closeDataView = () => {
    setDataView({})
    setActiveDataView(false)
  }

  const eventOnDataLoaded = (data) => {
    setPaginationData(data)
  }

  const onChangedFilterModel = (key, value) => {
    setFilterModel((prevState) => ({
      ...prevState,
      [key]: value,
    }))
  }

  const onClickedButtonFilter = () => {
    tableBodyRef.current.getMonitoringData(filterModel, 1, 12)
  }

  const paginate = (filter, page, pageSize) => {
    tableBodyRef.current.getMonitoringData(filterModel, page, pageSize)
  }

  return (
    <div className="d-flex">
      <div>
        <div className={styles.table}>
          <div className={styles.tableDiv}>
            <table>
              <TableHeaderComponent
                onClickedButtonFilter={onClickedButtonFilter}
                onChangedFilterModel={onChangedFilterModel}
                activeDataView={activeDataView}
              />
              <TableBodyComponent
                ref={tableBodyRef}
                activeDataView={activeDataView}
                displayDataView={displayDataView}
                eventOnDataLoaded={eventOnDataLoaded}
              />
            </table>
          </div>
          <TableFooterComponent
            paginate={paginate}
            decrementPageButtonNumbers={decrementPageButtonNumbers}
            paginateTo={paginateTo}
            incrementPageButtonNumbers={incrementPageButtonNumbers}
            currentPage={paginationData.currentPage}
            pageSize={paginationData.pageSize}
            totalData={paginationData.totalCount}
            pageButtons={pageButtons}
            activeDataView={activeDataView}
          />
        </div>
      </div>
      <div
        style={{
          display: activeDataView ? null : 'none',
          paddingLeft: '10px',
        }}
      >
        <div className={styles.dataViewPanel}>
          <DataViewComponent closeDataView={closeDataView} data={dataView} />
        </div>
      </div>
    </div>
  )
}

export default TableComponent
