import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'

import styles from './log-in.module.css'
import VirtualaiLogo from '../../assets/icons/VirtualAiLogo.png'
import signInIcon from '../../assets/icons/Sign-In.png'
import eyeIcon from '../../assets/icons/Eye.png'

import authService from '../../core/auth.service'

function LoginComponent(props) {
  const history = useHistory()

  const [email, setEmail] = useState()
  const [password, setPassword] = useState()
  const [showPassword, setShowPassword] = useState('password')
  const [visibleErrDialog, setVisibleErrDialog] = useState(false)

  const handleLogin = () => {
    const userInfo = {
      email: email,
      password: password,
    }
    authService
      .login(userInfo)
      .then((res) => {
        authService.setUserIdentity(res.data)
        props.loginEvent()
        history.push(`/monitoring`)
      })
      .catch((err) => {
        setVisibleErrDialog(true)
      })
  }

  const redirectToForgotPasswordPage = () => {
    history.push('/forgot-password')
  }

  const togglePassword = () => {
    if (showPassword === 'password') {
      setShowPassword('text')
    } else {
      setShowPassword('password')
    }
  }

  return (
    <>
      <div className={styles.body}>
        <div className={styles.panel}>
          <div className="container">
            <div className="row py-3 text-center">
              <p className={styles.title}>
                <img
                  className={`${styles.virtualAiLogo} mt-4 mb-4`}
                  src={VirtualaiLogo}
                  alt="VirtualAiLogo"
                />
                <br />
                Sign In
              </p>
              <input
                className={styles.emailInput}
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              <br />
              <div className="input-group position-relative">
                <input
                  className={styles.passwordInput}
                  type={showPassword}
                  placeholder="Pasword"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <div className={`${styles.eyeIcon} input-group-append`}>
                  <img
                    style={{ cursor: 'pointer' }}
                    src={eyeIcon}
                    alt="EyeIcon"
                    onClick={togglePassword}
                  />
                </div>
              </div>
              <p
                className={styles.errorAlertText}
                style={{ display: visibleErrDialog ? null : 'none' }}
              >
                Invalid email or password, please try again
              </p>
              <button
                className={styles.signInButton}
                style={{ marginTop: visibleErrDialog ? '0px' : null }}
                onClick={handleLogin}
              >
                Sign In{' '}
                <img
                  className={styles.signInIcon}
                  src={signInIcon}
                  alt="SignInIcon"
                />
              </button>
              <p
                className={styles.redirectUrlText}
                style={{ cursor: 'pointer' }}
                onClick={redirectToForgotPasswordPage}
              >
                Forgot password?
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default LoginComponent
